export default window.onload = function () {
    window.onscroll = function (e) {
        var top = $(this).scrollTop();//,
        // console.log(top);
        // var go1Top = $("#tuijian").offset().top - $("#tuijian").height();//-=
        if (top > 100)//
        {
            $("#celan").css({ display: "block" });
        }
        else {
            $("#celan").css({ display: "none" });
        }
    }
}


